<template>
    <grid-layout
        :layout.sync="layout"
        :row-height="rowHeight"
        :is-draggable="isDraggable"
        :is-resizable="isResizable"
        :vertical-compact="verticalCompact"
        :use-css-transforms="useCssTransforms"
        :col-num="colNum"
        :margin="margin">
        <grid-item v-for="(item, index) in layout" :key="item.i" :class="item.cssClass"
            :x="item.x"
            :y="item.y"
            :w="item.w"
            :h="item.h"
            :i="item.i"
            :item="item"
            :is-draggable="getPropsValue(item.isDraggable, isDraggable, defVal.isDraggable)"
            :is-resizable="getPropsValue(item.isResizable, isResizable, defVal.isResizable)"
            :min-h="item.minH || defVal.minH"
            :max-h="item.maxH || defVal.maxH"
            :min-w="item.minW || defVal.minW"
            :max-w="item.maxW || defVal.maxW"
            :style="{backgroundColor: backgroundColor}"
            @resize="resize"
            @move="move"
            @resized="resized"
            @moved="moved">
            <button @click="closeWidget(index)"></button>
            <component :is="item.type" :item-info="item"></component>
        </grid-item>
    </grid-layout>
</template>

<script>
    import GridItem from './components/GridItem.vue';
    import GridLayout from './components/GridLayout.vue';

    import Widget from './components/Widget.vue';

    import { deepCopy, isNil } from './utils/util';

    export default {
        name: 'app',
        components: {
            // ResponsiveGridLayout,
            GridLayout,
            GridItem,
        },
        // 
        addWidgetType(type, widget){
            this.components[type] = {
                ...widget,
                extends: Widget
            }
        },
        props: {
            isDraggable: { // 
                type: Boolean,
                default: true
            },
            isResizable: { // 
                type: Boolean,
                default: true
            },
            rowHeight: { // 
                type: Number,
                default: 150
            },
            maxRows: { // 
                type: Number,
                default: Infinity
            },
            margin: {
                type: Array,
                default: () => [10, 10]
            }, // 
            verticalCompact: { // 
                type: Boolean,
                default: true
            },
            useCssTransforms: { // css transforms
                type: Boolean,
                default: true
            },
            colNum: { // 
                type: Number,
                default: 12
            },
            backgroundColor: {
                type: String,
                default: 'rgba(200,200,200,1)'
            }
        },
        data () {
            return {
                layout: [], // 
                defVal: {
                    minH: 1,
                    minW: 1,
                    // maxH: Infinity,
                    maxH: 4,
                    maxW: 4,
                    isDraggable: true,
                    isResizable: true
                }
            }
        },
        mounted: function () {
            this.index = this.layout.length;
        },
        methods: {
            getPropsValue(itemValue, globalValue, defaultValue){
                if(!isNil(itemValue)) return itemValue;
                if(!isNil(globalValue)) return globalValue;
                return defaultValue;
            },
            // layout
            setLayout(layout){
                this.layout = deepCopy(layout);
            },
            // layout
            getLayout(){
                return deepCopy(this.layout);
            },
            // 
            closeWidget: function(index) {
                this.layout.splice(index, 1);
            },
            addItem: function(opts) {
                let defOpts = {
                    x: 0,
                    y: 0,
                    w: 2,
                    h: 2,
                    i: this.index + ''
                }
                let item = Object.assign(defOpts, opts);
                this.index++;
                this.layout.push(item);
            },
            move: function(item){
                this.$emit('move', item);
            },
            resize: function(item, newSize){
                this.$emit('resize', item, newSize);
            },
            moved: function(item){
                this.$emit('moved', item);
            },
            resized: function(item, newSize){
                this.$emit('resized', item, newSize);
            }
        },
    }
</script>
